import * as Utils from './utils'

const code = document.querySelector('code.fun')
Utils.splitText(code).querySelectorAll('.lttr').forEach(letter => {
  letter.style.display = 'inline-block'
  if (letter.textContent.match(/\s/g)) {
    letter.textContent = '.'
    letter.style.opacity = 0.2
  }
})

requestAnimationFrame(update)

function update () {
  code.querySelectorAll('.lttr').forEach(letter => {
    const rect = letter.getBoundingClientRect()
    const val = 1000 - Math.hypot(Utils.mouse.absX - rect.x, Utils.mouse.absY - rect.y) * 5
    // console.log(val)
    letter.style.fontVariationSettings = `'wght' ${val}`
  })
  // const val = 1000 - Math.hypot(Utils.mouse.x - 0.5, Utils.mouse.y - 0.5) * 2000
  // code.style.fontVariationSettings = `'wght' ${val}`
  requestAnimationFrame(update)
}
